<template>
	<svg class="icon" aria-hidden="true">
		<use :xlink:href=fullIcon></use>
	</svg>
</template>

<script>
	export default {
		name: "MySvgIcon",
		props: {
			icon: {
				type: String,
				required: true,
			}
		},
		computed: {
			fullIcon() {
				return `#icon-${this.icon}`;
			}
		}
	}
</script>

<style lang="scss" scoped>
	.icon {
		width: 1em;
		height: 1em;
		vertical-align: -0.15em;
		fill: currentColor;
		overflow: hidden;
	}
</style>