<template>
  <div v-if="!route.hidden">
    <!-- 如果没有子菜单 -->
    <template v-if="routeHasOneShowingChild(route.children, route)">
      <my-link :to="resolvePath(oneChildRoute.path)">
        <el-menu-item :index="resolvePath(oneChildRoute.path)">
          <my-item :icon="oneChildRoute.meta && oneChildRoute.meta.icon"
                   :title="oneChildRoute.meta && oneChildRoute.meta.title"></my-item>
        </el-menu-item>
      </my-link>
    </template>
    <!-- 如果有子菜单，需要递归 -->
    <el-submenu v-else :index="resolvePath(route.path)">
      <template slot="title">
        <my-item :icon="route.meta && route.meta.icon"
                 :title="route.meta && route.meta.title"></my-item>
      </template>
      <!-- 嵌套递归 -->
      <sidebar-item v-for="child in route.children" :key="child.path" :route="child" :base-path="resolvePath(child.path)"></sidebar-item>
    </el-submenu>
  </div>
</template>

<script>
  import MyLink from './MyLink';
  import MyItem from './MyItem';
  import {isExternal} from "@/utils/validate";
  import path from 'path'

  export default {
    name: "SidebarItem",
    components: {
      MyLink,
      MyItem,
    },
    props: {
      route: {
        type: Object,
        required: true,
      },
      basePath: {
        type: String,
        default: ''
      }
    },
    data(){
      return {
        oneChildRoute: null // 只有 1 个孩子的 route
      }
    },
    methods: {
      routeHasOneShowingChild(children = [], route) {
        const showingChildren = children.filter(item => !item.hidden);
        if (showingChildren.length === 0) {
          this.oneChildRoute = {...route, path: ''}
          return true;
        } else if (showingChildren.length === 1) {
          this.oneChildRoute = showingChildren[0]
          return true;
        }
        // 有 2 个 child，说明是有子菜单的
        return false;
      },
      resolvePath(routePath){
        if(isExternal(routePath)) return routePath
        if(isExternal(this.basePath)) return this.basePath
        return path.resolve(this.basePath, routePath)
      }
    }
  }
</script>
