<template>
  <div :class="{'sidebar-has-logo': $store.state.settings.showSidebarLogo}">
    <logo :collapse="collapseSidebar"></logo>
    <el-scrollbar>
      <el-menu
         :style="{'height': $store.state.settings.showSidebarLogo ? 'calc(100vh - 50px)' : '100vh'}"
         :background-color="variables.sidebarBgColor"
         :text-color="variables.sidebarTextColor"
         :active-text-color="variables.sidebarActiveTextColor"
         :collapse="collapseSidebar"
         :collapse-transition="false"
         :default-active="defaultActiveMenu">
        <sidebar-item v-for="route in routes" :key="route.path" :route="route" :base-path="route.path"/>
      </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Logo from './Logo'
  import SidebarItem from './SidebarItem'
  import variables from '@/styles/variables.scss'

  export default {
    name: "Sidebar",
    components: {
      Logo,
      SidebarItem
    },
    computed: {
      routes() {
        return this.$router.options.routes;
      },
      variables() {
        return variables;
      },
      ...mapGetters(['collapseSidebar']),
      defaultActiveMenu() {
        const route = this.$route
        const { meta, path } = route
        console.log(meta);
        console.log(path);
        if (meta.activeMenu) {
          return meta.activeMenu
        }
        return path
      }
    }
  }
</script>