import axios from 'axios'
import querystring from 'querystring'

function callback1(response, resolve, reject, loading) {
  if (loading) {
    loading.close();
  }
  let {data, message, status} = response.data;
  if (status === 'SUCCESS') {
    resolve(response.data);
    if(message) {
      this.$message.success(message);
    }
  } else if (status === 'FAILURE') {
    this.$message.error(message);
    // reject(response);
  }
}

function callback2(error, reject, loading) {
  if (loading) {
    loading.close();
  }
  this.$message.error(error);
  reject(error);
}


function get(url, data) {
  return new Promise((resolve, reject) => {
    axios({
      method: 'get',
      url: url,
      params: data,
    }).then((response) => {
      callback1.call(this, response, resolve, reject);
    }, (error) => {
      callback2.call(this, error, reject);
    })
  })
}

function getLoading(url, data) {
  const loading = this.$loading({
    lock: true,
    text: 'Loading',
  });
  return new Promise((resolve, reject) => {
    axios({
      method: 'get',
      url: url,
      params: data,
    }).then((response) => {
      callback1.call(this, response, resolve, reject, loading);
    }, (error) => {
      callback2.call(this, error, reject, loading);
    })
  })
}

function post(url, data) {
  return new Promise((resolve, reject) => {
    axios({
      method: 'post',
      url: url,
      data: querystring.stringify(data),
    }).then((response) => {
      callback1.call(this, response, resolve, reject);
    }, (error) => {
      callback2.call(this, error, reject);
    })
  })
}

