function postJson(url, data) {
  return new Promise((resolve, reject) => {
    axios({
      method: 'post',
      url: url,
      data: JSON.stringify(data),
      headers: {'Content-Type': 'application/json'}
    }).then((response) => {
      callback1.call(this, response, resolve, reject);
    }, (error) => {
      callback2.call(this, error, reject);
    })
  })
}

