<template>
  <el-breadcrumb separator="/" class="breadcrumb-container">
    <transition-group name="breadcrumb">
      <el-breadcrumb-item v-for="(item, index) in breadcrumbList" :key="item.path">
        <span v-if="breadcrumbList.length-1 === index" class="breadcrumb-without-link">{{item.meta.title}}</span>
        <a v-else @click.prevent="handleLink(item)">{{item.meta.title}}</a>
      </el-breadcrumb-item>
    </transition-group>
  </el-breadcrumb>
</template>

<script>
  export default {
    name: "Breadcrumb",
    data() {
      return {
        breadcrumbList: null
      }
    },
    watch: {
      $route() {
        this.getBreadcrumbList()
      }
    },
    created() {
      this.getBreadcrumbList()
    },
    methods: {
      getBreadcrumbList() {
        let matched = this.$route.matched.filter(item => item.meta && item.meta.title)
        const first = matched[0]
        if (!this.isDashboard(first)) {
          matched = [{path: '/dashboard', meta: {title: 'Dashboard'}}].concat(matched)
        }
        this.breadcrumbList = matched
      },
      isDashboard(route) {
        const routeName = route && route.name
        if (!routeName) return false
        return routeName.trim().toLocaleLowerCase() === 'Dashboard'.toLocaleLowerCase();
      },
      handleLink(route) {
        const {redirect, path} = route
        if (redirect) {
          this.$router.push(redirect)
        } else {
          this.$router.push(path)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .breadcrumb-container {
    a {
      font-size: 14px;
      font-weight: normal;
      cursor: pointer;
    }
    .breadcrumb-without-link {
      color: #97a8be;
      cursor: text;
    }
  }
</style>