<template>
  <div class="navbar-container" :class="{'fix-header': fixHeader, 'collapse-sidebar': collapseSidebar}">
    <hamburger class="hamburger-container"
               :collapse-sidebar="collapseSidebar"
               @toggleCollapseSidebar="toggleCollapseSidebar"></hamburger>
    <breadcrumb class="breadcrumb-container"></breadcrumb>

    <div class="right-container">

      <el-button @click="changeShowSidebarLogo">changeShowSidebarLogo</el-button>
      <el-button @click="changeFixHeader">changeFixHeader</el-button>

      <el-dropdown trigger="click">
        <span>
          <img :src="avatar" class="avatar-img"/>
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <router-link to="/">
            <el-dropdown-item>Home</el-dropdown-item>
          </router-link>
          <a href="#">
            <el-dropdown-item>Github</el-dropdown-item>
          </a>
          <a href="#">
            <el-dropdown-item>Docs</el-dropdown-item>
          </a>
          <a @click.prevent="logout">
            <el-dropdown-item divided>Logout</el-dropdown-item>
          </a>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Hamburger from '@/components/Hamburger.vue'
  import Breadcrumb from '@/components/Breadcrumb.vue'

  export default {
    name: "Navbar",
    components: {
      Hamburger,
      Breadcrumb
    },
    data() {
      return {
        avatar: 'https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif?imageView2/1/w/80/h/80'
      }
    },
    computed: {
      ...mapGetters(['fixHeader', 'collapseSidebar'])
    },
    methods: {
      toggleCollapseSidebar() {
        this.$store.dispatch('sidebar/toggleCollapseSidebar')
      },
      async logout() {
        await this.$store.dispatch('user/logout')
        this.$router.push(`/login?redirect=${this.$route.fullPath}`)
      },
      changeShowSidebarLogo() {
        this.$store.dispatch('settings/changeShowSidebarLogo')
      },
      changeFixHeader() {
        this.$store.dispatch('settings/changeFixHeader')
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "@/styles/variables.scss";
  @import "@/styles/mixin.scss";

  .navbar-container {
    @include clearfix;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, .1);
    transition: all .5s;
    &.fix-header {
      position: fixed;
      left: $sidebarWidth;
      right: 0;
      background-color: #fff;
      &.collapse-sidebar {
        left: $sidebarCollapseWidth;
      }
    }

    .hamburger-container {
      float: left;
      padding: 15px;
      height: 50px;
    }

    .breadcrumb-container {
      float: left;
      height: 50px;
      line-height: 50px;
    }

    .right-container {
      float: right;
      margin-right: 30px;
      height: 50px;
      line-height: 50px;

      .avatar-img {
        cursor: pointer;
        width: 40px;
        height: 40px;
        border-radius: 10px;
      }
    }
  }
</style>