import requestService from '@/utils/requestService'

export function login(data) {
  return requestService({
    method: 'post',
    url: '/user/login',
    data
  })
}

export function getInfo(token) {
  return requestService({
    method: 'get',
    url: '/user/info',
    params: {token}
  })
}

export function logout() {
  return requestService({
    method: 'post',
    url: '/user/logout'
  })
}