const createRouter = () => new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: () => ({y: 0}),
  routes: myRoutes
})

const index = createRouter()

export function resetRouter() {
  const newRouter = createRouter()
  index.matcher = newRouter.matcher
}

export default index