<template>
  <!-- 不加下行会报错，eslint报错，具体解决：https://github.com/vuejs/eslint-plugin-vue/issues/462 -->
  <!-- eslint-disable-next-line vue/require-component-is -->
  <component v-bind="linkProps(to)">
    <slot/>
  </component>
  <!-- <component>是vue的一个内置组件，参考：https://cn.vuejs.org/v2/api/#%E5%86%85%E7%BD%AE%E7%9A%84%E7%BB%84%E4%BB%B6 -->
</template>

<script>
  import {isExternal} from "@/utils/validate";

  export default {
    name: "MyLink",
    props: {
      to: {
        type: String,
        required: true
      }
    },
    methods: {
      linkProps(url) {
        if (isExternal(url)) {
          return {
            is: 'a',
            href: url,
            target: '_blank',
            rel: 'noopener'
          }
        } else {
          return {
            is: 'router-link',
            to: url
          }
        }
      }
    }
  }
</script>