/**
 * 用户基本信息
 *   token
 *   name
 *   avatar
 *   introduction
 *   roles
 * */

import {getCookieToken, setCookieToken, removeCookieToken} from "@/utils/cookieToken";
import router, {resetRouter} from "@/router";
import urls from "@/utils/urls";
import {login, getInfo, logout} from "@/api/user"

const state = {
  token: getCookieToken(),
  name: '',
  avatar: '',
  introduction: '',
  roles: []
}

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  }
}

const actions = {
  /**
   * 登陆，从后台获取 token（根据用户名、密码、时间戳生成token）
   * 把 token 存入 vuex 和 cookie
   * */
  login({commit}, userInfo) {
    const {username, password} = userInfo
    console.log('登陆用户信息：', username, password)
    return new Promise((resolve, reject) => {
      login(userInfo).then(response => {
        debugger
        console.log('登陆后台返回 token:', response)
        const token = 'token-token-token'
        commit('SET_TOKEN', token)
        setCookieToken(token)
        resolve(response)
      }, error => {
        reject(error)
      })
    })
  },
  /**
   * 从后台获取登陆的用户基本信息，存入vuex
   * */
  getInfo({commit, state}) {
    const {token} = state
    console.log("getInfo 从后台获取登陆的用户基本信息，存入vuex:", token)
    return new Promise(resolve => {
      getInfo().then(response => {
        const data = {
          name: 'yhguo',
          avatar: 'xxx',
          introduction: 'yyy',
          roles: ['admin', 'developer', 'tester']
        }
        const {name, avatar, introduction, roles} = data
        if (!roles || roles.length <= 0) {
          console.error('getInfo: roles must be a non-null array!')
        }
        commit('SET_NAME', name)
        commit('SET_AVATAR', avatar)
        commit('SET_INTRODUCTION', introduction)
        commit('SET_ROLES', roles)
        resolve(200)
      })
    })
  },
  /**
   * 登出，移除 vuex 里的token，cookie头里的token
   * 移除所有用户信息
   * 重置路由
   * */
  logout({commit, state}) {
    const {token} = state.token
    console.log('登出，清除 token：', token)
    return new Promise(() => {
      logout().then(response => {
        console.log('logout response:', response);
        commit('SET_TOKEN', '')
        removeCookieToken()
        commit('SET_NAME', '')
        commit('SET_AVATAR', '')
        commit('SET_INTRODUCTION', '')
        commit('SET_ROLES', [])
      })
    })
  },
  /**
   * 动态改变路由，
   * */
  changeRoles({commit, dispatch}, newRole) {

    return new Promise(async resolve => {

      console.log('changeRoles - ', newRole)

      const token = 'token-token-token'
      commit('SET_TOKEN', token)
      setCookieToken(token)

      // const {roles} = await dispatch('getInfo')

      // 重置路由
      resetRouter()
      // 新的可用路由
      const accessRoutes = await dispatch('access/generateRoutes', ['admin'], {root: true})
      // 添加
      router.addRoutes(accessRoutes)

      resolve(200)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}