import Vue from 'vue'
import MySvgIcon from '@/components/MySvgIcon'

// 注册全局 Icon 组件
Vue.component('my-svg-icon', MySvgIcon)

// 引入 svg 目录下的所有以 .svg 结尾的 svg 图标文件
// 这样，需要新增图标时，仅需在 src/icons/svg 文件夹下新增一个对应的 svg 文件即可
const req = require.context('./svg', false, /\.svg$/)
const requireAll = requireContext => {
    requireContext.keys().map(requireContext)
}
requireAll(req)