import Cookies from 'js-cookie'
import settings from '@/settings/index'

const {showSidebarLogo, fixHeader} = settings

const state = {
  showSidebarLogo: Cookies.get('showSidebarLogo') ? !!+Cookies.get('showSidebarLogo') : showSidebarLogo,
  fixHeader: Cookies.get('fixHeader') ? !!+Cookies.get('fixHeader') : fixHeader
}

const mutations = {
  CHANGE_SHOW_SIDEBAR_LOGO: state => {
    state.showSidebarLogo = !state.showSidebarLogo
    // 把值放在 cookie 里面
    if(state.showSidebarLogo){
      Cookies.set('showSidebarLogo', 1)
    } else {
      Cookies.set('showSidebarLogo', 0)
    }
  },
  CHANGE_FIX_HEADER: state => {
    state.fixHeader = !state.fixHeader
    // 把值放在 cookie 里面
    if(state.fixHeader){
      Cookies.set('fixHeader', 1)
    } else {
      Cookies.set('fixHeader', 0)
    }
  }
}

const actions = {
  changeShowSidebarLogo({commit}){
    commit('CHANGE_SHOW_SIDEBAR_LOGO')
  },
  changeFixHeader({commit}){
    commit('CHANGE_FIX_HEADER')
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}