import Cookies from 'js-cookie'

const state = {
  // 收缩菜单，只展示图标
  collapseSidebar: Cookies.get('collapseSidebar') ? !!+Cookies.get('collapseSidebar') : false
}

const mutations = {
  TOGGLE_COLLAPSE_SIDEBAR: state => {
    state.collapseSidebar = !state.collapseSidebar
    // 把值放在 cookie 里面
    if(state.collapseSidebar){
      Cookies.set('collapseSidebar', 1)
    } else {
      Cookies.set('collapseSidebar', 0)
    }
  }
}

const actions = {
  toggleCollapseSidebar({commit}){
    commit('TOGGLE_COLLAPSE_SIDEBAR')
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}