import Vue from 'vue'
import App from './App.vue'
import router from '@/router'
import store from './store'

import 'normalize.css/normalize.css'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import locale from 'element-ui/lib/locale/lang/en'
Vue.use(ElementUI, { locale })

// 全局 CSS 样式
import '@/styles/global.scss'

// svg 图标
import '@/icons'

// 路由权限相关
import '@/access'

// http 请求相关
import {
  get,
  getLoading,
  post,
  postLoading,
  postFile,
  postFileLoading,
  postJson,
  postJsonLoading
} from '@/utils/http';
Vue.prototype.$get = get;
Vue.prototype.$getLoading = getLoading;
Vue.prototype.$post = post;
Vue.prototype.$postLoading = postLoading;
Vue.prototype.$postFile = postFile;
Vue.prototype.$postFileLoading = postFileLoading;
Vue.prototype.$postJson = postJson;
Vue.prototype.$postJsonLoading = postJsonLoading;

// 设置为 false 以阻止 vue 在启动时在控制台生成生产提示。
Vue.config.productionTip = false

new Vue({
  router: router,
  store,
  render: h => h(App)
}).$mount('#app')
