import settings from '@/settings/index'
const title = settings.title

/**
 * document.title
 * */
export function getPageTitle(pageTitle) {
  if(pageTitle){
    return `${pageTitle} - ${title}`
  }
  return `${title}`
}

/**
 *
 * */
export function param2Obj(url) {
  const search = url.split('?')[1]
  if (!search) {
    return {}
  }
  return JSON.parse(
      '{"' +
      decodeURIComponent(search)
          .replace(/"/g, '\\"')
          .replace(/&/g, '","')
          .replace(/=/g, '":"')
          .replace(/\+/g, ' ') +
      '"}'
  )
}